
export const addMessageToStore = (state, payload) => {
  const { message, sender } = payload;
  // if sender isn't null, that means the message needs to be put in a brand new convo
  if (sender !== null) {
    const newConvo = {
      id: message.conversationId,
      otherUser: sender,
      messages: [message],
    };
    newConvo.latestMessageText = message.text;
    return [newConvo, ...state];
  }

  return state.map((convo) => {
    if (convo.id === message.conversationId) {
      const convoCopy = { ...convo };
      convoCopy.messages.push(message);
      convoCopy.latestMessageText = message.text;

      return convoCopy;
    } else {
      return convo;
    }
  });
};

export const processConversations = (conversations) => {
  //If the unseen messages were sent by this user, mark as seen:
  const processedConvos = conversations.map((convo) => {
    const {messages, otherUser} = convo;
    const lastMessage = messages[messages.length-1];
    if (lastMessage && lastMessage.senderId === otherUser.id) {
      return convo;
    } else {
      return {...convo, unseenCount: 0}
    }
  })
  return processedConvos;
}

export const addOnlineUserToStore = (state, id) => {
  return state.map((convo) => {
    if (convo.otherUser.id === id) {
      const convoCopy = { ...convo };
      convoCopy.otherUser.online = true;
      return convoCopy;
    } else {
      return convo;
    }
  });
};

export const removeOfflineUserFromStore = (state, id) => {
  return state.map((convo) => {
    if (convo.otherUser.id === id) {
      const convoCopy = { ...convo };
      convoCopy.otherUser.online = false;
      return convoCopy;
    } else {
      return convo;
    }
  });
};

export const addSearchedUsersToStore = (state, users) => {
  const currentUsers = {};

  // make table of current users so we can lookup faster
  state.forEach((convo) => {
    currentUsers[convo.otherUser.id] = true;
  });

  const newState = [...state];
  users.forEach((user) => {
    // only create a fake convo if we don't already have a convo with this user
    if (!currentUsers[user.id]) {
      let fakeConvo = { otherUser: user, messages: [] };
      newState.push(fakeConvo);
    }
  });

  return newState;
};

export const updateConversationAsSeen = (state, id) => {
  const newState = state.map((convo) => {
    if (convo.id === id) {
      const newConvo = {...convo, unseenCount: 0}
      return newConvo;
    } else {
      return convo;
    }
  })
  return newState;
}

export const incrementUnseenCount = (state, id) => {
  const newState = state.map((convo) => {
    if (convo.id === id) {
      const {unseenCount} = convo;
      return {...convo, unseenCount: unseenCount+1};
    } else {
      return convo;
    }
  })
  return newState;
}

export const addNewConvoToStore = (state, recipientId, message) => {
  return state.map((convo) => {
    if (convo.otherUser.id === recipientId) {
      const newConvo = { ...convo };
      newConvo.id = message.conversationId;
      newConvo.messages.push(message);
      newConvo.latestMessageText = message.text;
      return newConvo;
    } else {
      return convo;
    }
  });
};

export const setLastMessageRead = (state, {messageId, convoId}) => {
  const newState = state.map((convo) => {
    if (convo.id === convoId) {
      return {...convo, lastMessageReadId: messageId};
    } else {
      return convo;
    }
  })
  return newState;
}

export const turnOnTypingForConvo = (state, id) => {
  return state.map((convo) => {
    if (convo.id === id) {
      return {...convo, isTyping: true}
    }
    return convo;
  });
}

export const turnOffTypingForConvo = (state, id) => {
  return state.map((convo) => {
    if (convo.id === id) {
      return {...convo, isTyping: false}
    }
    return convo;
  });
}
