import React from "react";
import { Box, Typography, Badge } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    justifyContent: "space-between",
    marginLeft: 20,
    flexGrow: 1,
    height: '100%',
    overflow: 'auto',
  },
  username: {
    fontWeight: "bold",
    letterSpacing: -0.2,
  },
  previewText: {
    fontSize: 12,
    color: "#9CADC8",
    letterSpacing: -0.17,
  },
  italicText: {
    fontStyle: "italic",
    fontSize: 12,
    color: "#9CADC8",
    letterSpacing: -0.17,
  },
  notification: {
    height: 20,
    width: 20,
    backgroundColor: "#3F92FF",
    marginRight: 20,
    color: "white",
    fontSize: 10,
    letterSpacing: -0.5,
    fontWeight: "bold",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 10,
  },
}));

const ChatContent = (props) => {
  const classes = useStyles();

  const { conversation } = props;
  const { latestMessageText, otherUser, unseenCount, isTyping} = conversation;
  const textStyling = (isTyping ? classes.italicText : classes.previewText);
  return (
    <Box className={classes.root}>
      <Box>
        <Typography className={classes.username}>
          {otherUser.username}
        </Typography>

        <Typography className={textStyling}>
          {!isTyping && latestMessageText}
          {isTyping && "Typing..."}
        </Typography>
      </Box>
      {unseenCount > 0 && <Box className={classes.notification}>
        <Badge badgeContent={unseenCount} color="primary" max={999} />
      </Box>}
    </Box>
  );
};

export default ChatContent;
