import axios from "axios";
import socket from "../socket";
import {broadcastTypingAction} from "../socket";
import {
  gotConversations,
  addConversation,
  setNewMessage,
  setSearchedUsers,
  setConversationAsSeen,
} from "../actions/conversationActions";
import { gotUser, setFetchingStatus } from "./userActions";
import {broadcastMessageSeen} from '../socket';

axios.interceptors.request.use(async function (config) {
  const token = await localStorage.getItem("messenger-token");
  config.headers["x-access-token"] = token;

  return config;
});

// USER THUNK CREATORS
export const fetchUser = () => async (dispatch) => {
  dispatch(setFetchingStatus(true));
  try {
    const { data } = await axios.get("/auth/user");
    dispatch(gotUser(data));
    if (data.id) {
      socket.emit("go-online", data.id);
    }
  } catch (error) {
    console.error(error);
  } finally {
    dispatch(setFetchingStatus(false));
  }
};

export const register = (credentials) => async (dispatch) => {
  try {
    const { data } = await axios.post("/auth/register", credentials);
    await localStorage.setItem("messenger-token", data.token);
    dispatch(gotUser(data));
    socket.emit("go-online", data.id);
  } catch (error) {
    console.error(error);
    dispatch(gotUser({ error: error.response.data.error || "Server Error" }));
  }
};

export const login = (credentials) => async (dispatch) => {
  try {
    const { data } = await axios.post("/auth/login", credentials);
    await localStorage.setItem("messenger-token", data.token);
    dispatch(gotUser(data));
    socket.emit("go-online", data.id);
  } catch (error) {
    console.error(error);
    dispatch(gotUser({ error: error.response.data.error || "Server Error" }));
  }
};

export const logout = (id = null) => async (dispatch) => {
  try {
    await axios.delete("/auth/logout");
    await localStorage.removeItem("messenger-token");
    dispatch(gotUser({}));
    id && socket.emit("logout", id);
  } catch (error) {
    console.error(error);
  }
};


// CONVERSATIONS THUNK CREATORS
export const fetchConversations = () => async (dispatch) => {
  try {
    const { data } = await axios.get("/api/conversations");
    dispatch(gotConversations(data));
  } catch (error) {
    console.error(error);
  }
};

const saveMessage = async (body) => {
  const { data } = await axios.post("/api/messages", body);
  return data;
};

const sendMessage = (data, body) => {
  console.log(data)
  socket.emit("new-message", {
    message: data.message,
    recipientId: body.recipientId,
    sender: data.sender,
  });
};

// message format to send: {recipientId, text, conversationId}
// conversationId will be set to null if its a brand new conversation
export const postMessage = (body) => async (dispatch) => {
  try {
    const data = await saveMessage(body);
    const {conversationId, recipientId} = body;
    if (!conversationId) {
      dispatch(addConversation(recipientId, data.message));
    } else {
      dispatch(setNewMessage(data.message));
    }
    sendMessage(data, body);
    broadcastTypingAction(conversationId, recipientId, 'stoppedTyping');
  } catch (error) {
    console.error(error);
  }
};

export const markConvoAsSeen = (id, messageId, senderId) => async (dispatch) => {
  try {
    const { data } = await axios.patch(`/api/conversations/viewed/${id}/${senderId}/`);
    if (!data) {
      throw new Error(`Server request failed.`)
    }
    dispatch(setConversationAsSeen(id));
    broadcastMessageSeen(id, messageId, senderId)
  } catch (error) {
    console.error(error);
  }
};

//This function broadcasts the last message seen, saves updates to database. 
export const updateLastSeenExternally = (senderId, id, messageId) => async (dispatch) => {
  try {
    const { data } = await axios.patch(`/api/conversations/markSeen/${id}/${senderId}/${messageId}`);
    if (!data) {
      throw new Error(`Server request failed.`)
    }
    broadcastMessageSeen(id, messageId, senderId)
  } catch (error) {
    console.error(error);
  }
};

export const searchUsers = (searchTerm) => async (dispatch) => {
  try {
    const { data } = await axios.get(`/api/users/${searchTerm}`);
    dispatch(setSearchedUsers(data));
  } catch (error) {
    console.error(error);
  }
};
